<template>
  <v-content class="ma-0 pa-0">
    <v-row>
      <v-col sm="12"> <app-panel :loading="loading"     :count="total.count" :amount="total.amount"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="FTD"   :count="ftd.count" :amount="ftd.amount"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="Retention"   :count="rtn.count" :amount="rtn.amount"></app-panel> </v-col>
    </v-row>
  </v-content>
</template>
<script>
 

// import $ from 'jquery' 
// import dateformat from 'dateformat'
import { mapActions } from 'vuex'
 import AppPanel from './Panel'
export default {
  name: 'WakelTotal',
  components: {
    AppPanel
  },
  props:{
    dates: Array
  },
  data () {
    return {
      loading: false,
      total: { count: 0, amount: 0},
      ftd: { count: 0, amount: 0},
      rtn: { count: 0, amount: 0},
     
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotals',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    async fetchTotals () {
      this.loading = true

      let ftd =    await this.get({ collection: 'department', query: {
        type: 'ftd'
      } })
      ftd = ftd.data.docs[0]._id

      let rtn =    await this.get({ collection: 'department', query: {
        type: 'rtn'
      } })
      rtn = rtn.data.docs[0]._id
       
      const total = await this.get({ collection: 'dashboard/total', query: {
        created_on: this.dates.join('|')
      } })
      if (total.status == 200 && total.data.length) {
        const [ allTotal ] = total.data
        this.total.amount = allTotal.amount ? allTotal.amount   : 0
        this.total.count = allTotal.count ? allTotal.count   : 0
      }


      const ftdTotal = await this.get({ collection: 'dashboard/total', query: {
        created_on: this.dates.join('|'),
        department: ftd
      } })
      if (ftdTotal.status == 200 && ftdTotal.data.length) {
        const [ ftdallTotal ] = ftdTotal.data
        this.ftd.amount = ftdallTotal.amount ? ftdallTotal.amount   : 0
        this.ftd.count = ftdallTotal.count ? ftdallTotal.count   : 0
      }
      

      const rtnTotal = await this.get({ collection: 'dashboard/total', query: {
        created_on: this.dates.join('|'),
        department: rtn
      } })
      if (rtnTotal.status == 200 && rtnTotal.data.length) {
        const [ rtnallTotal ] = rtnTotal.data
        this.rtn.amount = rtnallTotal.amount ? rtnallTotal.amount   : 0
        this.rtn.count = rtnallTotal.count ? rtnallTotal.count   : 0
      }








      // const total = await this.get({ collection: 'transaction/total', query: {
      //   created_on: this.dates.join('|')
      // } })
     
      // if (total.status == 200 && total.data.length) {
      //   const [ allTotal ] = total.data
        
      //   this.subtotal.amount = allTotal.subtotal ? allTotal.subtotal.amount : 0
      //   this.subtotal.count = allTotal.subtotal ? allTotal.subtotal.count : 0

      //   this.withdraw.amount = allTotal.withdraw ? allTotal.withdraw.amount : 0
      //   this.withdraw.count = allTotal.withdraw ? allTotal.withdraw.count : 0

      //   this.totalSAR.amount = allTotal.amount ? allTotal.amount : 0
      //   this.totalSAR.count = allTotal.count ? allTotal.count : 0

      //   this.totalUSD.amount = allTotal.amount ? allTotal.amount / 3.9  : 0
      //   this.totalUSD.count = allTotal.count ? allTotal.count   : 0
      
      // }

      this.loading = false
    }
  } 
 
}
</script>
