<template>
  <v-content class="ma-0 pa-0">
    <v-row>
      <v-col sm="6"> <app-panel :loading="loading" title="Subtotal" currency="SAR"  :count="subtotal.count" :amount="subtotal.amount"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="Withdraw" currency="SAR" :count="withdraw.count" :amount="withdraw.amount" color="red"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="Total SAR" currency="SAR" :count="totalSAR.count" :amount="totalSAR.amount"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="Total USD" :count="totalUSD.count" :amount="totalUSD.amount"></app-panel> </v-col>
    </v-row>
  </v-content>
</template>
<script>
 

// import $ from 'jquery' 
// import dateformat from 'dateformat'
import { mapActions } from 'vuex'
 import AppPanel from './Panel'
export default {
  name: 'WakelTotal',
  components: {
    AppPanel
  },
  props:{
    dates: Array
  },
  data () {
    return {
      loading: false,
      subtotal: { count: 0, amount: 0},
      withdraw: { count: 0, amount: 0},
      totalSAR: { count: 0, amount: 0},
      totalUSD: { count: 0, amount: 0},
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotals',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    async fetchTotals () {
      this.loading = true
      const total = await this.get({ collection: 'transaction/total', query: {
        created_on: this.dates.join('|')
      } })
      this.loading = false
      if (total.status == 200 && total.data.length) {
        const [ allTotal ] = total.data
        
        this.subtotal.amount = allTotal.subtotal ? allTotal.subtotal.amount : 0
        this.subtotal.count = allTotal.subtotal ? allTotal.subtotal.count : 0

        this.withdraw.amount = allTotal.withdraw ? allTotal.withdraw.amount : 0
        this.withdraw.count = allTotal.withdraw ? allTotal.withdraw.count : 0

        this.totalSAR.amount = allTotal.amount ? allTotal.amount : 0
        this.totalSAR.count = allTotal.count ? allTotal.count : 0

        this.totalUSD.amount = allTotal.amount ? allTotal.amount / 3.9  : 0
        this.totalUSD.count = allTotal.count ? allTotal.count   : 0
      
      }
    }
  } 
 
}
</script>
