<template>
<v-row>
  <v-data-table 
  :headers="headers"
  :expanded="expanded"
  show-expand
  single-expand
 item-key="_id"
  :items="items"
  :loading="loading" 
  style="width:100%"
>
    <template v-slot:loading   >
        <v-skeleton-loader
        class="mt-6"
        height="94"
        type="list-item-three-line"
      >
      </v-skeleton-loader>
    </template>
    <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
    </template>   

    <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length" class="pa-4">
          <v-data-table :headers="dataHeaders" :items="item.child" >
            <template v-slot:item.amount="{ item }" >
                {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
            </template>   
            
            <template v-slot:item.created_on="{ item }"  >
              {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
            </template>
            
            
          </v-data-table>
        </td>
      </template> 
      <template v-slot:body.append="{ headers }"   >
           <tr>
             <td :colspan="headers.length-2" class="font-weight-bold">Total</td>

             <td class="font-weight-bold">{{ count }}</td>
             <td class="font-weight-bold">{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(total) }}</td>
           </tr>
      </template>
  </v-data-table>
</v-row>
</template>

<script>
import dateformat from 'dateformat'
import utility from './../../utility'
 
import { mapActions } from 'vuex'
export default {
  name: 'AgentReport',
  data () {
    return {
      dateformat,
      headers: [{
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'amount' }],
      dataHeaders: [{
            text: 'Department',
            value: 'department.name',
        },
        {
          text: 'Date',
          value: 'created_on',
        },
        
        { text: 'Amount', value: 'amount' },
        { text: 'Owner', value: 'user.displayName' }],
      items: [],
      loading: false,
      total: 0,
      count: 0,
      expanded: []
    }
  },
  computed:{
    user () {
      return this.$store.state.auth.user
    }
  },
  methods:{
    ...mapActions({
      get: 'api/get'
    })
  },
  async created () {
    this.loading = true
    const result = await this.get({
        collection: 'report_new',
        query: {
          department: this.user.department || null,
          by: 'user',
          created_on: utility.currentDateRange.join('|'),
          id: this.user._id  
        }
      })
     if (result.data[0]) {
       this.total = result.data[0].amount
       this.count = result.data[0].count

       this.items = result.data[0].child
     } 
     
     this.loading = false
  }
}
</script>